import React from 'react';
import './center.scss'

class DisplayBox extends React.Component{
    constructor(props){
        super(props)
        this.state={name:'',id:'personal'}
        this.handleClick=this.handleClick.bind(this)
    }
    handleClick() {
        var target = event.target.id
        var text =document.getElementById(target).textContent
        this.setState({name:text,id:target})

    }
    componentDisMount(){
        document.getElementById('personal').addEventListener('click',this.handleClick)
        document.getElementById('recentOrder').addEventListener('click',this.handleClick)
    }
    render(){
        let whichOne=(this.state.id == 'personal')?this.props.box1:this.props.Box2
        return(
            <article className='displayBoard'>
                <ul className="navBar">
                    <li id='personal' onClick={this.handleClick}><h3></h3></li>
                <li><h3></h3>
                        <ul>
                            <li id='recentOrder'></li>
                            <li id='comments'></li>
                            <li id='chargeBack'></li>
                        </ul>
                    </li>
                <li><h3></h3>
                        <ul>
                            <li id='packet'></li>
                            <li id='remainder'></li>
                            <li id='integral'></li>
                        </ul>
                    </li>
                <li><h3></h3>
                        <ul>
                            <li id='personalData'></li>
                            <li id='address'></li>
                            <li id='safe'></li>
                            <li id='modifyPassword'></li>
                        </ul>
                    </li>
                </ul>
                {whichOne}
                </article>
        )
    }
}
class PerCenter extends React.Component{
    render(){
        return(
                <section className="info">
                    < ul className='baseInfo'>
                        <li>
                            <ul className='title'>
                                <li><img src='../../image/info.png'/></li>
                                <li></li>
                                <li></li>
                            </ul>
                        </li>
                        <li><span>0</span></li>
                        <li><span>0</span></li>
                        <li><span>0.00</span></li>
                    </ul>
                <div className='orderInfo'>
                    <h3></h3>
                    <p></p>
                </div>

                    <ul className='prviate'>
                        <li></li>
                        <li></li>
                    </ul>
                </section>
        )
    }
}
class Content extends React.Component{
    render(){
        return(
            <section className='content'>
                <h4>{this.state.name}</h4>
                <p>{this.props.content}</p>
            </section>
        )
    }
}

class ShowBox extends React.Component{
    render(){
        return <DisplayBox box1={<PerCenter/>} box2={<Content/>}/>
    }
}

export default ShowBox






